.item {
  background-color:#2b2a2a;
  border-radius: 8px; 
  box-shadow: 2px 4px 4px 0000009f;
  padding: 12px;
  margin-bottom: 8px;
  position: relative;
  cursor: pointer;
  h3 {
    margin-bottom: 8px;
    word-break: break-all;
  }

  span {
    color: #D0D0D0;
  }

  @media screen and (min-width:1280px) {
    font-size: 1.8rem;
  }
}

.itemSelecionado {
  background-color: #2b2a2a;
  box-shadow: 2px 4px 4px #0000009F inset;
  border: solid 3px rgb(58, 57, 57);
}

.itemCompletado {
  background-color: #919290;
  cursor: not-allowed;

  .concluido {
    display: block;
    background-image: url('../../../assets/img/check-mark.svg');
    background-repeat: no-repeat;
    background-size: 38px 38px;
    position: absolute;
    top: 50%;
    right: 12px;
    transform: translateY(-50%);
    width: 42px;
    height: 43px;
  }
}